<template lang="pug">
  div.user-card 
    div.top-bg 
    div.user-info 
    div.add-friend( @click="addFriend" ) 加好友
</template>
<script>
import { socketEmit } from '@/socket/socket'
export default {
  data() {
    return {
      userInfo: {}
    }
  },
  created() {
    socketEmit('getUserInfo', {account: this.$route.params.account}).then(res => {
      this.userInfo = res.data;
    }).catch(err => {
      console.log(err)
    });
  },
  
  methods: {
    addFriend() {
      let { _id } = this.userInfo;
      socketEmit('addFriend', { to: _id }).then(res => {
        console.log(res, '..')
      })
    }
  }
}
</script>





